import React from "react"
import { Tooltip } from "@mui/material"

const FullScreenBtn = ({
  onClick,
  paddingTop,
  display,
  paddingLeft,
  position,
  backgroundColor,
  marginTop,
  marginLeft,
  tooltipText,
  icon,
  cursor,
  right,
  top,
  zIndex,
  fullscreenEnabled,
  isFullscreen
}) => {
  return (
    <>
      <Tooltip title={tooltipText} placement="top" sx={{ pointerEvents: "visible" }}>
        <button
          disabled={fullscreenEnabled ? true : false}
          // disabled={isFullscreen ? true : false}
          data-test-id="fullScreenBtn"
          onClick={onClick}
          style={{
            // boxShadow: "0 0 0 2px rgba(0,0,0,.1)",
            filter: "drop-shadow(black 0px 0px 13.5px)",
            background: "transparent",
            ":hover": { backgroundColor: "black" },
            pointerEvents: "visible",
            backgroundColor: backgroundColor || "transparent",
            width: "40px",
            height: "40px",
            borderRadius: "50px",
            border: "none",
            display: display || "flex",
            marginTop: marginTop,
            marginLeft: marginLeft,
            alignContent: "center",
            justifyContent: "center",
            alignItems: "center",
            outlined: 1,
            cursor: cursor,
            paddingTop: paddingTop,
            paddingLeft: paddingLeft,
            position: position,
            top: top || "16px",
            right: right,
            zIndex: zIndex || 100
          }}>
          {icon}
        </button>
      </Tooltip>
    </>
  )
}

export default FullScreenBtn
