import React, { useState } from "react"
import { PercentageBar, displayPerc } from "./PercentageBar"
import MDBox from "../MDBox"
import { DialogActions, InputAdornment, TextField } from "@mui/material"
import MDTypography from "../MDTypography"
import { supabase } from "../../supabaseClient"
import { useParams } from "react-router-dom"
import { useToast } from "@dalgu/react-toast"
import MDButton from "../MDButton"
import { useLocation } from "react-router-dom"

export const InvestForm = ({ personalProgress, networkProgress, missingProgress, updateParent, closeDialolgue, currentBalance, duration }) => {
  const houseId = useParams().id
  const { pathname } = useLocation()
  const { showToast } = useToast()
  const [personalProgressState, setPersonalProgressState] = useState(personalProgress) // Initial value

  const [amount, setAmount] = useState() //should be 0 or undefined?
  const handlePersonalProgressChange = event => {
    const value = parseFloat(event.target.value)

    setAmount(formatAmount(value - personalProgress))
    setPersonalProgressState(value)
  }

  const diff = personalProgressState - personalProgress
  const total = personalProgress + networkProgress + missingProgress
  const personalProgressPercentage = (personalProgress / total) * 100
  const networkProgressPercentage = (networkProgress / total) * 100

  const currentProgress = networkProgressPercentage + personalProgressPercentage
  const handleKeyPress = e => {
    if (e.key === "Enter") {
      addTransaction()
    }
  }
  const addTransaction = async () => {
    const { error } = await supabase.rpc("create_investing_transaction", {
      new_destination: houseId,
      new_amount: parseFloat(amount)
    })

    if (error) showToast(error.message)
    else {
      console.log("updating self should be called here")
      updateParent(amount)
      showToast("Investment added")
      setAmount(0)
    }
    closeDialolgue()
  }
  let message
  const monthsText = duration === 1 ? "month" : "months"
  const theseText = duration === 1 ? "" : "these"

  const currentPercentage = displayPerc(((personalProgress + (Number(amount) || 0)) / total) * 100)
  message = `You will gain ${
    currentPercentage > 0 ? currentPercentage + "%" : "a percentage"
  } of the property's profits over ${theseText} ${duration} ${monthsText}!`

  if (amount < 0.01 && amount !== "") {
    message = "Amount must be at least 0.01 USDT!"
  } else if (amount > missingProgress) {
    message = "Amount cannot exceed missing investments!"
  } else if (amount > currentBalance) {
    message = "Amount cannot exceed available balance!"
  }
  return (
    <>
      <MDBox p={3} sx={{ margin: 0, lineHeight: 1 }}>
        <TextField
          data-test-id="invest-input-amount"
          type="number"
          placeholder="0"
          sx={{ width: "100%", minWidth: "80px", mb: 0 }}
          id="amount"
          label="Amount"
          variant="outlined"
          max={missingProgress}
          inputProps={{
            type: "number",
            pattern: "[0-9]*"
          }}
          InputLabelProps={{ shrink: true }}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <MDTypography fontSize={11} fontWeight="medium" color={"info"}>
                  USDT
                </MDTypography>
              </InputAdornment>
            )
          }}
          value={amount}
          onChange={e => {
            let inputValue = formatAmount(e.target.value)
            if (inputValue > missingProgress) inputValue = missingProgress
            setAmount(parseFloat(inputValue))
            setPersonalProgressState(Math.min(personalProgress + (parseFloat(inputValue) || 0), missingProgress + personalProgress))
          }}
          onKeyDown={handleKeyPress}
        />
        <MDBox display="flex" justifyContent="start" alignItems="center" mt={2}>
          <MDTypography sx={{ mr: 1 }} variant="button" textTransform="capitalize" fontSize={16}>
            Current Balance:
          </MDTypography>
          <MDBox display="flex" justifyContent="space-between" flexDirection="column">
            <MDTypography
              mr="3px"
              alignItems="baseline"
              display="flex"
              fontWeight="medium"
              justifyContent="end"
              color={"info"}
              variant="button"
              textTransform="capitalize"
              fontSize={16}>
              {pathname !== "/tutorial/1" ? (amount < currentBalance || !amount ? formatAmount(currentBalance - (amount || 0)) : 0) : "5000"}
              <MDTypography fontWeight="medium" pl={0.3} fontSize={"0.55rem"} variant={"caption"} color={"info"}>
                USDT
              </MDTypography>
            </MDTypography>
          </MDBox>
        </MDBox>
        <div style={{ position: "relative" }}>
          <PercentageBar networkProgress={networkProgress} personalProgress={personalProgressState} missingProgress={missingProgress - diff} interactive />
          <input
            style={{
              WebkitAppearance: "none",
              background: "none",
              position: "absolute",
              top: "-4px",
              width: 100 - currentProgress + 1 + "%",
              marginLeft: currentProgress - 0.5 + "%",
              accentColor: "rgb(30, 174, 255)"
            }}
            type="range"
            min={personalProgress}
            max={missingProgress + personalProgress}
            step="1"
            value={personalProgressState}
            onChange={handlePersonalProgressChange}
          />
        </div>

        <MDTypography variant="caption" sx={{ lineHeight: 1.5, letterSpacing: 0, display: "block", height: "18px", mt: 3 }}>
          {message}
        </MDTypography>
      </MDBox>
      <DialogActions>
        <MDButton onClick={closeDialolgue}>Cancel</MDButton>
        <MDButton
          color="info"
          data-test-id="invest-btn-invest"
          variant="gradient"
          onClick={addTransaction}
          disabled={amount <= 0 || !amount || amount > missingProgress || currentBalance === 0 || amount > currentBalance}>
          Invest
        </MDButton>
      </DialogActions>
    </>
  )
}
const formatAmount = inputValue => {
  // Ensure there's only two decimal places
  //remove leading zero
  inputValue = inputValue.toString()
  //if (inputValue[0] === "0" && inputValue[1] !== "." && inputValue[1] !== "," && inputValue) inputValue = inputValue.slice(1)
  //remove leading dot
  if (inputValue[0] === ".") inputValue = "0" + inputValue
  //remove leading comma
  if (inputValue[0] === ",") inputValue = "0" + inputValue

  const parts = inputValue.split(".")
  if (parts[1] && parts[1].length > 2) {
    inputValue = parts[0] + "." + parts[1].slice(0, 2)
  }
  return inputValue
}
