import React from "react"
export const PercentageBar = ({ personalProgress, networkProgress, missingProgress, interactive, marginTop }) => {
  // Ensure percentages are numbers and limit them to 1 decimal place
  const total = personalProgress + networkProgress + missingProgress
  personalProgress = displayPerc((personalProgress / total) * 100)
  networkProgress = displayPerc((networkProgress / total) * 100)
  missingProgress = displayPerc((missingProgress / total) * 100)

  // Calculate the total percentage
  const totalPercentage = personalProgress + networkProgress + missingProgress

  // If the total percentage is greater than 100, adjust the percentages proportionally
  if (totalPercentage > 100) {
    const factor = 100 / totalPercentage
    personalProgress *= factor
    networkProgress *= factor
    missingProgress *= factor
  }

  // Determine if spacing is needed between labels
  const needSpacingAtEnd = missingProgress > 0 && missingProgress < 10 && Math.abs((interactive ? personalProgress : networkProgress) - missingProgress) < 10
  const needSpacingAtStart = personalProgress > 0 && personalProgress < 10 && Math.abs(networkProgress - personalProgress) < 10

  return (
    <>
      <div style={{ display: "flex", borderRadius: "25px", overflow: "hidden", marginTop: marginTop || "27px" }}>
        {interactive ? (
          <>
            <div style={{ height: "8px", width: `${networkProgress}%`, backgroundColor: "rgb(26, 115, 232)", minWidth: networkProgress && "3px" }}></div>
            <div style={{ height: "8px", width: `${personalProgress}%`, backgroundColor: "rgb(30, 174, 271)", minWidth: personalProgress && "3px" }}></div>
            <div style={{ height: "8px", width: `${missingProgress}%`, backgroundColor: "rgb(206, 212, 218)", minWidth: missingProgress && "3px" }}></div>
          </>
        ) : (
          <>
            <div style={{ height: "8px", width: `${personalProgress}%`, backgroundColor: "rgb(30, 174, 271)", minWidth: personalProgress && "3px" }}></div>
            <div style={{ height: "8px", width: `${networkProgress}%`, backgroundColor: "rgb(26, 115, 232)", minWidth: networkProgress && "3px" }}></div>
            <div style={{ height: "8px", width: `${missingProgress}%`, backgroundColor: "rgb(206, 212, 218)", minWidth: missingProgress && "3px" }}></div>
          </>
        )}
      </div>
      {interactive ? (
        <div style={{ display: "flex", paddingTop: "5px", gap: (needSpacingAtEnd || needSpacingAtStart) && "5px" }}>
          {networkProgress > 0 && <PercentageLabel percentage={networkProgress} color="rgb(26, 115, 232)" />}
          {personalProgress > 0 && <PercentageLabel percentage={personalProgress} color="rgb(30, 174, 271)" />}
          {missingProgress > 0 && !needSpacingAtEnd && <PercentageLabel percentage={missingProgress} color="#7b809a" />}
        </div>
      ) : (
        <div style={{ display: "flex", paddingTop: "5px", gap: (needSpacingAtEnd || needSpacingAtStart) && "5px" }}>
          {personalProgress > 0 && <PercentageLabel percentage={personalProgress} color="rgb(30, 174, 271)" />}
          {networkProgress > 0 && <PercentageLabel percentage={networkProgress} color="rgb(26, 115, 232)" />}
          {missingProgress > 0 && <PercentageLabel percentage={missingProgress} color="#7b809a" />}
        </div>
      )}
    </>
  )
}

// Function to limit decimal places and ensure a minimum value of 0.1%
export function displayPerc(percentage) {
  // Check if the percentage is not a number or is NaN (e.g., if it's undefined or null)
  if (isNaN(percentage)) {
    return 0 // Return 0 in such cases
  }

  // Check if the percentage is less than 0.1% and not 0
  if (percentage > 0 && percentage < 0.1) {
    return 0.1 // Return 0.1% in this case
  }

  // Convert to number and limit to 1 decimal place
  return parseFloat(percentage.toFixed(1))
}

function PercentageLabel({ percentage, color }) {
  return (
    <span
      style={{
        width: `${percentage}%`,
        color: color,
        fontSize: "10px",
        lineHeight: "1.25",
        letterSpacing: "0.03333em",
        paddingTop: "4px",
        textAlign: "center",
        minWidth: "fit-content"
      }}>
      {percentage.toFixed(1)}%
    </span>
  )
}
