import React, { useEffect, useState } from "react"
import MDBox from "../../components/MDBox"
import { useLocation } from "react-router-dom"

const Gallery = ({
  isFullscreen,
  images,
  isHeightToggled,
  marginTopImg,
  fullScreenGallery,
  fullscreenEnabled,
  clickedImageIndex,
  handleClickedEnterFullScreen,
  handleClickedExitFullScreen
}) => {
  const { pathname } = useLocation()
  return (
    <>
      {!fullScreenGallery && (
        <MDBox
          sx={{
            overflow: fullscreenEnabled ? "hidden" : "auto",
            position: "absolute",
            pointerEvents: "none",
            backdropFilter: "blur(13px)",
            maskImage: "linear-gradient(0deg, rgba(0,0,0,1) 33%, rgba(216,216,216,0) 45%)",
            zIndex: 9,
            left: 0,
            width: "100%",
            height: "35svh",
            top: pathname === "/tutorial/1" ? "33svh" : "12svh"
          }}></MDBox>
      )}
      <MDBox
        sx={{
          display: !isFullscreen ? "flex" : "none",
          justifyContent: "space-between",
          flexDirection: fullScreenGallery ? "column" : "row",
          margin: 0,
          top: 0,
          left: 0,
          padding: "0 !important",
          backgroundColor: "transparent",
          height: fullScreenGallery ? "100svh" : "36svh",
          WebkitBoxReflect: "below 0px linear-gradient(to bottom, rgba(0,0,0,0.0) 80%, rgba(0,0,0,1))"
        }}>
        <div
          className="scrollGallery"
          style={{
            overflowX: "scroll",
            flexWrap: isHeightToggled ? "wrap" : "nowrap"
          }}>
          {images?.map((image, index) => (
            <img
              key={index}
              onClick={() => handleClickedEnterFullScreen(index)}
              src={image}
              alt={`House ${index + 1}`}
              style={{
                borderTopLeftRadius: pathname === "/tutorial/1" && index === 0 ? "20px" : "0px",
                borderTopRightRadius: pathname === "/tutorial/1" && index === 0 ? "20px" : "0px",
                backgroundColor: "white",
                objectFit: "cover",
                scrollSnapAlign: "start",
                transition: "transform 0.3s ease",
                maxWidth: !isHeightToggled || index === images.length - 1 || index === 0 || images.length <= 3 ? "100%" : "50%",
                minWidth: !isHeightToggled || index === images.length - 1 || index === 0 || images.length <= 3 ? "100%" : "50%",
                //width: !isHeightToggled || index === images.length - 1 || images.length <= 3 ? "100vw" : "50%",
                height: !fullScreenGallery || images.length <= 3 ? "36svh" : index === 0 || index === images.length - 1 ? "36svh" : "18svh",
                marginTop: index === 0 ? "0px" : marginTopImg
                // marginBottom: fullScreenGallery && index === images.length - 1 ? "38px" : "0px"
              }}
              fetchpriority="height"
              loading="eager"
            />
          ))}
        </div>
      </MDBox>
      {clickedImageIndex !== null && (
        <div
          style={{
            height: pathname === "/tutorial/1" ? "70svh" : "95svh",
            display: isFullscreen ? "flex" : "none",
            alignItems: "center",
            justifyContent: "end",
            width: "100%",
            left: "0px",
            position: "relative",
            overflow: "hidden",
            zIndex: 11
          }}
          onClick={() => handleClickedExitFullScreen()}>
          <img
            src={images[clickedImageIndex]}
            alt={`House ${clickedImageIndex + 1}`}
            style={{
              width: "100vw",
              objectFit: "contain",
              display: fullScreenGallery ? "hidden" : "flex"
            }}
            fetchpriority="height"
            loading="eager"
          />
        </div>
      )}
    </>
  )
}

export default Gallery
