import { Routes, Route, Navigate, Link, useLocation, useSearchParams } from "react-router-dom"
import routes from "./routes"
import "./asset/css/global.css"
import MenuTab from "./components/MenuTab"
import { useEffect, useState } from "react"
import NoInternetConnection from "./components/OfflinePage/NoInternetConnection"
import { useAuth } from "./components/context"
import MDBox from "./components/MDBox"
import { FullscreenControl, Map, Marker } from "react-map-gl"
import MDTypography from "./components/MDTypography"
import { useQuery } from "@supabase-cache-helpers/postgrest-swr"
import { supabase } from "./supabaseClient"
import ErrorFallback from "./components/Suspense/ErrorBoundary/ErrorFallback"
import LogoLoading from "./components/Suspense/CoverLoading/LogoLoading"
import FullScreenBtn from "./components/FullScreenBtn"
import fullScreenIcon from "./icons/fullscreenIcon"
import NormalSizeScreenIcon from "./icons/normalSizeScreenIcon"

function App() {
  const [online, setOnline] = useState(true)
  const [tooltipContent, setTooltipContent] = useState(null)
  const { pathname } = useLocation()
  const [showMap, setShowMap] = useState(false)
  const [fullMap, setFullMap] = useState(false)
  const [isFullScreenBtnVisible, setIsFullScreenBtnVisible] = useState(false)

  const user = useAuth()
  let { error: marketError, data: marketData, isLoading } = useQuery(supabase.rpc("get_market"))

  // const [filteredMarketData, setFilteredMarketData] = useState([])

  // useEffect(() => {
  //   if (marketData) {
  //     const filteredData = marketData.filter(house => house.total_investments > 0)
  //     setFilteredMarketData(filteredData)
  //   }
  // }, [marketData])

  const checkOnlineStatus = async () => {
    try {
      const response = await fetch("/1pixel.png", {
        method: "HEAD",
        cache: "no-store"
      })
      return response.status >= 200 && response.status < 300
    } catch (error) {
      return false
    }
  }
  const handleMarkerClick = (event, house) => {
    setTooltipContent(oldHouse => (oldHouse === house ? null : house))
  }

  const handleBtnMap = () => {
    console.log("before click", isFullScreenBtnVisible)
    setIsFullScreenBtnVisible(!isFullScreenBtnVisible)
    console.log("after click", isFullScreenBtnVisible)
  }

  const [mapRef, setMapRef] = useState()

  const fullScreen = () => {
    setFullMap(prevMap => !prevMap)
    setTimeout(() => mapRef.resize(), 1)
  }

  useEffect(() => {
    if (pathname === "/market") {
      setShowMap(true)
    }
  }, [pathname])
  useEffect(() => {
    if (window.location.hostname !== "localhost" && window.location.hostname !== "127.0.0.1" && window.location.hostname !== "")
      // eslint-disable-next-line no-undef
      progressier.add({
        id: user.user_metadata?.username
      })
    blockZooming()
  }, [])
  const [mapHeight, setMapHeight] = useState("50vh")
  const [mapLatitude, setMapLatitude] = useState("34.82")
  const [mapGradient, setMapGradient] = useState("30%")
  const toggleMap = () => {
    setMapHeight(prevHeight => (prevHeight === "100vh" ? "50vh" : "100vh"))
    setMapLatitude(prevLatitude => (prevLatitude === "45.82" ? "34.82" : "45.82"))
    setMapGradient(prevHeight => (prevHeight === "30%" ? "0%" : "30%"))
  }

  const [searchParams, setSearchParams] = useSearchParams({ fullscreenEnabled: false })
  const fullscreenEnabled = searchParams.get("fullscreenEnabled") === "true"
  const makeFullScreen = () => {
    if (!fullscreenEnabled && document.documentElement.requestFullscreen) {
      document.documentElement.requestFullscreen()
    } else if (document.exitFullscreen) {
      document.exitFullscreen()
    }
    setSearchParams({ fullscreenEnabled: !fullscreenEnabled })
    setTimeout(() => mapRef.resize(), 1)
  }
  const getRoutes = allRoutes =>
    allRoutes.map(route => {
      if (route.route) {
        return <Route exact path={route.route} element={route.component} key={route.key} />
      }
      return null
    })
  if (marketError) return <ErrorFallback error={marketError.message} />
  if (isLoading) return <LogoLoading />
  return online ? (
    <>
      {showMap && (
        <>
          {/* {!fullMap && ( */}
          {!fullscreenEnabled && (
            <MDBox
              sx={{
                pointerEvents: "none",
                backdropFilter: "blur(13px)",
                maskImage: fullMap ? "none" : "linear-gradient(0deg, rgba(0,0,0,1) 33%, rgba(216,216,216,0) 45%)",
                zIndex: 9,
                position: "absolute",
                width: "100%",
                height: "30vh",
                top: "16vh",
                display: pathname !== "/market" && "none"
              }}></MDBox>
          )}
          <MDBox
            sx={{
              display: pathname !== "/market" && "none",
              WebkitBoxReflect: fullscreenEnabled ? "none" : "below 0px linear-gradient(to bottom, rgba(0,0,0,0.0) 80%, rgba(0,0,0,1))",
              marginTop: 0
            }}>
            {!document.documentElement.requestFullscreen && (
              <FullScreenBtn
                title={fullscreenEnabled ? "Enter Full screen" : "Exit Full Screen"}
                cursor="pointer"
                position="absolute"
                onWhite="#fff"
                right="11px"
                top={fullscreenEnabled ? "27px" : "9px"}
                icon={fullscreenEnabled ? <NormalSizeScreenIcon /> : fullScreenIcon}
                zIndex={1}
                onClick={makeFullScreen}
              />
            )}
            <Map
              onLoad={loadedMap => setMapRef(loadedMap.target)}
              mapboxAccessToken="pk.eyJ1IjoiZGF2aWRlc2NhbnUiLCJhIjoiY2xsamRvam13MThtdDNxbXBpaTJnbzltMyJ9.g4sxZVW0pJD_QUS8loyQJg"
              initialViewState={{
                longitude: 16.33,
                latitude: 42.82,
                zoom: 2.8,
                background: "#e6e7e9"
              }}
              style={{ height: fullscreenEnabled ? "100vh" : "36vh" }}
              mapStyle="mapbox://styles/davidescanu/clljnl38w019e01pl00xg9nia">
              <FullscreenControl
                zIndex={999}
                style={{
                  backgroundColor: "transparent",
                  boxShadow: "none",
                  filter: "drop-shadow(black 0px 0px 13.5px)",
                  marginTop: "-122px",
                  right: "4px",
                  borderRadius: "50px",
                  padding: "6.5px",
                  fontSize: "20px",
                  zIndex: 999
                }}
              />
              {marketData?.map(house => (
                <Marker
                  key={house?.house_id}
                  longitude={house?.coordinates?.lng}
                  latitude={house?.coordinates?.lat}
                  onClick={({ event }) => handleMarkerClick(event, house)}></Marker>
              ))}
              {tooltipContent && (
                <Marker latitude={tooltipContent.coordinates.lat} longitude={tooltipContent.coordinates.lng} offset={[0, 60]}>
                  <div style={{ padding: "10px" }}>
                    <div
                      style={{
                        background: "#fff",
                        borderRadius: "0.55rem",
                        width: "130px",
                        boxShadow: "0rem 0.25rem 0.375rem -0.0625rem rgba(0, 0, 0, 0.1), 0rem 0.125rem 0.25rem -0.0625rem rgba(0, 0, 0, 0.06)"
                      }}>
                      <Link to={`/investment/` + tooltipContent.house_id} rel="noreferrer">
                        <img
                          src={tooltipContent.pic}
                          alt={tooltipContent.title}
                          style={{ aspectRatio: "16/9", objectFit: "cover", width: "100%", height: "auto", borderRadius: "0.55rem 0.55rem 0% 0%" }}
                        />
                        <MDBox pb={1} px={1} display="flex" alignItems="center" justifyContent="space-between">
                          <MDTypography variant="h5" textTransform="capitalize" fontSize={"0.85rem"}>
                            {tooltipContent.title}
                          </MDTypography>
                          <MDBox display="flex" alignItems="center" justifyContent="center" flexDirection="column">
                            <MDTypography variant="button" color={"info"} fontWeight="medium" fontSize={"0.575em"}>
                              {tooltipContent.total_required}
                            </MDTypography>
                            <MDBox display="flex" justifyContent="end">
                              <MDTypography variant="button" color={"info"} fontWeight="medium" fontSize={"0.372em"}>
                                USDT
                              </MDTypography>
                            </MDBox>
                          </MDBox>
                        </MDBox>
                      </Link>
                    </div>
                  </div>
                </Marker>
              )}
            </Map>
          </MDBox>
        </>
      )}
      <div
        style={{
          padding:
            pathname.startsWith("/investment/") || pathname.startsWith("/user.user_metadata?.username") || pathname.startsWith("/tutorial/1")
              ? "0px"
              : "0 min(5vw,16px)",
          height: "100%"
        }}>
        {/* <div style={{ padding: "0 min(5vw,16px)", height: "100%" }}> */}
        <Routes>
          {getRoutes(routes)}
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      </div>
      {/* {!pathname.includes("register") && !pathname.includes("/login") && !fullMap && <MenuTab />} */}
      {!pathname.includes("register") && !pathname.includes("/login") && !fullscreenEnabled && <MenuTab />}
    </>
  ) : (
    <NoInternetConnection />
  )
}
function blockZooming() {
  // Block zooming
  document.addEventListener("gesturestart", function (e) {
    e.preventDefault()
  })

  document.addEventListener("gesturechange", function (e) {
    e.preventDefault()
  })
}
export default App
