import MDBox from "../../../components/MDBox"
import MDTypography from "../../../components/MDTypography"
import { Divider, Grid, Typography } from "@mui/material"
import DefaultInvestmentCard from "../../../examples/Cards/ProjectCards/DefaultInvestmentCard"

function SimilarHouses({ similar_houses, isLoading }) {
  return (
    <>
      <MDBox display="flex" justifyContent="start" alignItems="center" p={2}>
        <MDTypography variant="h6" sx={{ fontSize: "18px;" }}>
          Other Apartments
        </MDTypography>
      </MDBox>
      <Divider sx={{ mb: 0, mt: 0 }} />
      <MDBox>
        <Grid
          sx={{
            py: 2,
            px: 2,
            pb: 0
          }}
          container
          spacing={6}>
          <Grid item xs={12} md={6} xl={3}>
            {isLoading && "skeleton"}
            {similar_houses &&
              similar_houses.map((house, index) => (
                <DefaultInvestmentCard
                  id={house.house_id}
                  key={house.house_id}
                  addSpace={index !== similar_houses?.length - 1}
                  image={house.pic}
                  label={house.location}
                  title={house.title}
                  networkProgress={house.amount_reached - house.personal_invested_amount}
                  personalProgress={house.personal_invested_amount}
                  missingProgress={house.total_required - house.amount_reached}
                  duration={house.duration}
                  value={house.house_price}
                  priority={index < 5 ? "high" : "auto"}
                  loading={index < 5 ? "eager" : "lazy"}
                />
              ))}
            {similar_houses?.length === 0 && (
              <Typography
                variant="body1"
                sx={{
                  fontSize: "1rem",
                  lineHeight: 1.375,
                  fontWeight: 400,
                  letterSpacing: "0em",
                  pb: 2,
                  opacity: 1,
                  textTransform: "none",
                  verticalAlign: "unset",
                  color: "#7b809a",
                  margin: 0,
                  fontFamily: '"Roboto", "Helvetica", sansSerif'
                }}>
                There are no similar properties to show!{" "}
              </Typography>
            )}
          </Grid>
        </Grid>
      </MDBox>
    </>
  )
}
export default SimilarHouses
