import { Divider, Typography } from "@mui/material"
import MDTypography from "../../../../components/MDTypography"
import { Fragment } from "react"

function InvestmentDetails({ data }) {
  const renderLine = (label, value) => {
    if (typeof value === "object" && value !== null) {
      const nestedLines = Object.entries(value).map(([nestedLabel, nestedValue], index) => (
        <Fragment key={index}>
          {" "}
          {index === 0 && <Divider sx={{ mt: 5 }} />}
          {renderLine(nestedLabel, nestedValue)}
          {index !== Object.entries(value)?.length - 1 && <Divider />}{" "}
        </Fragment>
      ))

      return (
        <>
          <Line label={label} value={nestedLines} />
        </>
      )
    } else {
      return <Line label={label} value={value} />
    }
  }

  return (
    <>
      <MDTypography px={1.1} variant="h5" fontWeight="medium" sx={{ fontSize: "18px;" }}>
        Investment Details
      </MDTypography>
      <Divider />
      {data &&
        Object.entries(data).map(([label, value], index) => (
          <div key={index}>
            {renderLine(label, value)}
            {index !== Object.entries(data)?.length - 1 && <Divider />}{" "}
          </div>
        ))}
      {data === 0 && (
        <Typography
          py={1}
          pl={1}
          sx={{
            fontSize: "1rem",
            lineHeight: 1.375,
            fontWeight: 400,
            letterSpacing: "0em",
            opacity: 1,
            textTransform: "none",
            verticalAlign: "unset",
            color: "#7b809a",
            margin: 0,
            fontFamily: '"Roboto", "Helvetica", sansSerif'
          }}>
          There are no details regarding the investments.
        </Typography>
      )}
    </>
  )
}

function Line({ label, value }) {
  return (
    <div style={{ display: "flex", justifyContent: "space-between" }}>
      <MDTypography px={1.1} variant="h6" fontWeight="light" fontSize="15px">
        {label}:
      </MDTypography>
      <MDTypography px={1.1} variant="h6" fontWeight="light" fontSize="15px">
        {value}
      </MDTypography>
    </div>
  )
}
export default InvestmentDetails
