import Divider from "@mui/material/Divider"
import MDBox from "../../components/MDBox"
import MDTypography from "../../components/MDTypography"
import Dialog from "@mui/material/Dialog"
import DialogActions from "@mui/material/DialogActions"
import Slide from "@mui/material/Slide"
import { Card, Checkbox, FormControl, FormControlLabel, FormGroup, InputLabel, NativeSelect, TextField, Typography } from "@mui/material"
import "@inovua/reactdatagrid-community/index.css"
import MDButton from "../../components/MDButton"
import { forwardRef, useState, useEffect } from "react"
import { Calendar } from "react-calendar"
import MyInvestments from "./components/MyInvestments"
import ReportsLineChartNoCard from "../../examples/Charts/LineCharts/ReportsLineChartNoCard"
import reportsLineChartData from "../dashboard/data/reportsLineChartData"
import { supabase } from "../../supabaseClient"
import { useToast } from "@dalgu/react-toast"
import { Chart, registerables } from "chart.js"
import SearchIcon from "../../icons/SearchIcon"
import tuneIcon from "../../icons/tuneIcon"
import autographIconInvestments from "../../icons/autographIconInvestments"
import mapOutlinedIcon from "../../icons/mapOutlinedIcon"
import LogoLoading from "../../components/Suspense/CoverLoading/LogoLoading"
import GoBackBtn from "../../components/GoBackBtn/GoBackBtn"
import { useNavigate } from "react-router"

Chart.register(...registerables)

const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />
})

function InvestmentsListPage() {
  const { showToast } = useToast()
  const navigate = useNavigate()
  const { sales } = reportsLineChartData
  const [open, setOpen] = useState(false)
  const handleClickOpen = () => {
    return
    setOpen(true)
  }
  const [isOpen, setIsOpen] = useState(false)
  const handleClose = () => {
    setOpen(false)
  }
  const openSearch = () => {
    return
    setIsOpen(!isOpen)
  }
  const [investmentsData, setInvestmentsData] = useState(null)
  const [salesData, setSalesData] = useState(null)

  // const { data, error } = useQuery(supabase.rpc("get_investments_page"), { refreshInterval: 10000000 })

  useEffect(() => {
    const fetchData = async () => {
      const labels = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]

      const { data, error } = await supabase.rpc("get_investments_page")

      if (error) showToast(error.message)
      setInvestmentsData(data)

      const { amounts } = data
      const updatedSalesData = {
        labels,
        datasets: [
          {
            label: "Amount Invested",
            data: amounts,
            backgroundColor: "transparent",
            borderColor: "rgba(255, 255, 255, .8)",
            borderWidth: 4,
            fill: true,
            maxBarThickness: 6,
            pointBackgroundColor: "rgba(255, 255, 255, .8)",
            pointBorderColor: "transparent",
            pointRadius: 5,
            tension: 0
          }
        ]
      }
      setSalesData(updatedSalesData)
    }
    fetchData()
  }, [])

  const [orderBy, setOrderBy] = useState("name")
  const [showLocked, setShowLocked] = useState([])
  const [showUnlocked, setShowUnlocked] = useState([])

  const handleOrderChange = value => {
    setOrderBy(value)
  }

  const handleShowLockedChange = checked => {
    setShowLocked(checked)
  }

  const handleShowUnlockedChange = checked => {
    setShowUnlocked(checked)
  }

  const [filteredInvestments, setFilteredInvestments] = useState([])
  const [searchText, setSearchText] = useState("")
  const [selectedDateRange, setSelectedDateRange] = useState(null)
  const [isFilterApplied, setIsFilterApplied] = useState(false)

  const handleSearch = () => {
    let filteredInvestments = investmentsData?.my_properties.filter(investment => {
      const includesSearchText = investment.title.toLowerCase().includes(searchText.toLowerCase())
      const isInRange =
        selectedDateRange === null ||
        investment.invested_dates.some(date => {
          const investedDate = new Date(date)
          const startDate = new Date(selectedDateRange[0])
          const endDate = new Date(selectedDateRange[1])
          return investedDate >= startDate && investedDate <= endDate
        })

      const isLocked = investment.is_locked

      if (isLocked && !showLocked) {
        return false // Skip this investment if it's locked and showLocked is unchecked
      }

      if (!isLocked && !showUnlocked) {
        return false // Skip this investment if it's unlocked and showUnlocked is unchecked
      }

      return includesSearchText && isInRange
    })

    if (orderBy === "name") {
      filteredInvestments.sort((a, b) => a.title.localeCompare(b.title, undefined, { numeric: true, sensitivity: "base" }))
    } else if (orderBy === "date") {
      filteredInvestments.sort((a, b) => new Date(a.invested_dates[0]) - new Date(b.invested_dates[0]))
    } else if (orderBy === "invested_amount") {
      filteredInvestments.sort((a, b) => b.total_investments - a.total_investments)
    } else if (orderBy === "percentage_owned") {
      filteredInvestments.sort((a, b) => b.personal_invested_amount - a.personal_invested_amount)
    }

    setFilteredInvestments(filteredInvestments)
    setIsFilterApplied(true)
    handleClose()
  }
  if (investmentsData === null) return <LogoLoading />
  return (
    <MDBox sx={{ display: "flex", flexDirection: "column", height: "100%", justifyContent: "space-between" }}>
      <MDBox sx={{ margin: 0, padding: 0, pb: 11 }}>
        <MDBox pb={1.6} sx={{ alignItems: "center" }} display="flex" justifyContent="space-between">
          <MDBox sx={{ display: "flex", alignItems: "center", justifyContent: "center", transform: "translate(-12px, 0px)" }}>
            <GoBackBtn boxShadow="none" display="flex" paddingTop="12px" onWhite="#344767" />
            <MDTypography
              color={"whitwe"}
              sx={{
                fontSize: "1.5rem",
                pt: "11px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                letterSpacing: "0.00735em",
                lineHeight: "1.375",
                gap: "8px",
                fontFamily: "Roboto, Helvetica, Arial, sans-serif",
                fontWeight: 700,
                opacity: 1
              }}
              variant="h4">
              {autographIconInvestments} My Investments
            </MDTypography>
          </MDBox>
          <MDBox sx={{ pt: "21px", cursor: "pointer" }} onClick={() => navigate("/map")} data-test-id="my-investments-map">
            {mapOutlinedIcon}
          </MDBox>
        </MDBox>
        <>
          <Dialog
            open={open}
            TransitionComponent={Transition}
            keepMounted
            onClose={handleClose}
            aria-describedby="alert-dialog-slide-description"
            maxWidth="sm"
            fullWidth>
            <Calendar selectRange onChange={newDateRange => setSelectedDateRange(newDateRange)} />{" "}
            <MDBox mt={2.5} display="flex" justifyContent="space-around" alignItems="center">
              <MDBox maxWidth="30%">
                <InputLabel sx={{ mb: 0.5, overflow: "visible" }} variant="standard" htmlFor="uncontrolled-native">
                  Order by
                </InputLabel>
                <FormControl>
                  <NativeSelect
                    defaultValue={30}
                    inputProps={{
                      name: "order",
                      id: "uncontrolled-native"
                    }}
                    onChange={e => handleOrderChange(e.target.value)}>
                    <option value={"name"}>Name</option>
                    <option value={"date"}>Date</option>
                    <option value={"invested_amount"}>Invested amount</option>
                    <option value={"percentage_owned"}>Percentage owned</option>
                  </NativeSelect>
                </FormControl>
              </MDBox>
              <FormGroup>
                <FormControlLabel control={<Checkbox defaultChecked />} label="Show locked" onChange={e => handleShowLockedChange(e.target.checked)} />
                <FormControlLabel control={<Checkbox defaultChecked />} label="Show unlocked" onChange={e => handleShowUnlockedChange(e.target.checked)} />
              </FormGroup>{" "}
            </MDBox>
            <DialogActions>
              <MDButton onClick={handleClose}>Cancel</MDButton>
              <MDButton color="info" variant="gradient" onClick={handleSearch}>
                Filter
              </MDButton>
            </DialogActions>
          </Dialog>
          <Card
            sx={{
              position: "relative",
              py: 2,
              px: 2,
              mt: 3.8
            }}>
            <ReportsLineChartNoCard
              color="info"
              chart={sales}
              total_investments={investmentsData?.total_investments}
              locked_investments={investmentsData?.locked_investments}
              active_investments={investmentsData?.active_investments}
              completed_investments={investmentsData?.completed_investments}
              total_properties={investmentsData?.total_properties}
              active_properties={investmentsData?.active_properties}
              completed_properties={investmentsData?.completed_properties}
              locked_properties={investmentsData?.locked_properties}
              salesData={salesData}></ReportsLineChartNoCard>
          </Card>
          <Card
            sx={{
              mt: 3.7
            }}>
            <MDBox display="flex" justifyContent="space-between" alignItems="center" p={2}>
              {isOpen ? (
                <TextField
                  sx={{ width: "100%", minWidth: "80px", pb: 1 }}
                  id="outlined-basic"
                  label="Search..."
                  variant="outlined"
                  value={searchText}
                  onChange={e => setSearchText(e.target.value)}
                  onKeyPress={e => {
                    if (e.key === "Enter") {
                      handleSearch()
                    }
                  }}
                />
              ) : (
                <MDTypography pr={1} variant="h6" sx={{ fontSize: "18px;" }} lineHeight="1.375" fontWeight="700" letterSpacing="0em" opacity={1}>
                  My Properties
                </MDTypography>
              )}
              <MDBox mr={1} sx={{ display: "flex", alignItems: "center", justifyContent: "center", gap: "5px" }}>
                <SearchIcon onClick={openSearch} />
                <button
                  onClick={handleClickOpen}
                  style={{ border: "none", backgroundColor: "transparent", cursor: "pointer", fontSize: "20px", color: "rgb(52, 71, 103)" }}>
                  {tuneIcon}
                </button>
              </MDBox>
            </MDBox>
            <Divider sx={{ mb: 0, mt: 0 }} />
            <MDBox sx={{ display: "flex", flexDirection: "column", gap: 3, pt: 2.1 }}>
              <MyInvestments myProperties={investmentsData?.my_properties} />
            </MDBox>
          </Card>
        </>
      </MDBox>
    </MDBox>
  )
}
export default InvestmentsListPage
