import { Grid, Typography } from "@mui/material"
import DefaultInvestmentCard from "../../../../examples/Cards/ProjectCards/DefaultInvestmentCard"
import { Link, useParams } from "react-router-dom"
import trackEvent from "../../../../components/context"

function MyInvestments({ myProperties }) {
  const { id } = useParams()
  return (
    <>
      <Grid
        sx={{
          px: 2
        }}
        container
        spacing={3.7}>
        {myProperties &&
          myProperties?.map((property, index) => (
            <Grid key={property.house_id} item xs={12} md={6} xl={3}>
              <DefaultInvestmentCard
                image={property.pic}
                label={property.location}
                id={property.house_id}
                title={property.title}
                networkProgress={property.total_investments - property.personal_invested_amount}
                personalProgress={property.personal_invested_amount}
                missingProgress={property.total_required - property.total_investments}
                value={property.personal_invested_amount}
                caption="invested"
                duration={property.duration}
                priority={index < 5 ? "high" : "auto"}
                loading={index < 5 ? "eager" : "lazy"}
              />
            </Grid>
          ))}
        {!myProperties && (
          <Typography
            pt={3}
            pl={2}
            px={3.8}
            pb={2}
            variant="body1"
            sx={{
              fontSize: "1rem",
              lineHeight: 1.375,
              fontWeight: 400,
              letterSpacing: "0em",
              opacity: 1,
              textTransform: "none",
              verticalAlign: "unset",
              color: "#7b809a",
              margin: 0,
              fontFamily: '"Roboto", "Helvetica", sansSerif'
            }}>
            No investments yet. <br />
            <Link
              to="/market"
              underline="hover"
              style={{ color: "#7dd0f2" }}
              onClick={() => {
                trackEvent("MyPropertiesMakeYourFirstInvestment")
              }}>
              Go here
            </Link>{" "}
            to make your first one.
          </Typography>
        )}
      </Grid>
    </>
  )
}

export default MyInvestments
