import { useEffect, useState, React } from "react"
import { useLocation, useNavigate } from "react-router-dom"
import trackEvent, { useAuth } from "../context"
import NotificationIcon from "../../icons/NotificationIcon"
import { supabase } from "../../supabaseClient"
import { useQuery } from "@supabase-cache-helpers/postgrest-swr"
import { Logo } from "../../logos/logo"
import "./index.css"
import ErrorFallback from "../Suspense/ErrorBoundary/ErrorFallback"
import accountBalanceTabIcon from "../../icons/accountBalanceTabIcon"
import MDBox from "../MDBox"

const { Tabs, Tab } = require("@mui/material")

export default function MenuTab() {
  const { data: unreadNotificationsCount, error, mutate } = useQuery(supabase.rpc("get_unread_notifications_count"), { refreshInterval: 100000 })
  supabase
    .channel("table-db-changes")
    .on(
      "postgres_changes",
      {
        event: "INSERT",
        schema: "public",
        table: "notifications"
      },
      payload => (!payload.new.usernames || payload.new.usernames?.includes(user?.user_metadata?.username)) && mutate()
    )
    .subscribe()
  const [activeTabValue, setActiveTabValue] = useState(0)
  const { pathname } = useLocation()
  const navigate = useNavigate()
  const user = useAuth()

  const handleTabDashboard = () => {
    trackEvent("Dashboard")
    navigate("/dashboard")
  }

  const handleTabNotifications = () => {
    trackEvent("Notifications")
    navigate("/notifications")
    setTimeout(() => mutate(), 600) //update notifications count
  }

  const handleTabMarket = () => {
    trackEvent("Market")
    navigate("/market")
  }

  const handleTabBalance = () => {
    trackEvent("Balance")
    navigate("/balance")
  }

  const handleTabProfile = () => {
    trackEvent("Profile")
    navigate("/" + user?.user_metadata?.username)
  }

  useEffect(() => {
    document.documentElement.scrollTop = 0
    document.scrollingElement.scrollTop = 0
  }, [pathname])
  useEffect(() => {
    const handleTabChange = () => {
      if (pathname.startsWith("/investment/")) {
        setActiveTabValue(2)
        return
      }
      switch (pathname) {
        case "/balance":
          setActiveTabValue(3)
          break
        case "/notifications":
          setActiveTabValue(1)
          break
        case "/market":
          setActiveTabValue(2)
          break
        case `/${user?.user_metadata?.username}`:
        case "/settings":
          setActiveTabValue(4)
          break
        default:
          setActiveTabValue(0)
          break
      }
    }

    handleTabChange()
  }, [pathname])
  if (error) return <ErrorFallback error={error.message} />
  return (
    <>
      {/* <MDBox
        style={{
          position: "fixed",
          bottom: 0,
          width: "100%",
          borderTop: "1px solid #e2e2e2",
          zIndex: 214,
          height: "70px",
          backdropFilter: "blur(16px)",
          WebkitBackdropFilter: "blur(16px)",
          paddingBottom: "calc(env(safe-area-inset-bottom) - 1rem)",
          background: "#eff1f5d4"
        }}></MDBox> */}
      {/* <MDBox sx={{ height: "9vh", width: "100%" }}></MDBox> */}

      <MDBox
        style={{
          position: "fixed",
          bottom: 0,
          width: "100%",
          borderTop: "1px solid #e2e2e2",
          zIndex: 214,
          backdropFilter: "blur(16px)",
          WebkitBackdropFilter: "blur(16px)",
          paddingBottom: "calc(env(safe-area-inset-bottom) - 1rem)",
          background: "#eff1f5d4"
        }}>
        <Tabs
          TabIndicatorProps={{ sx: { backgroundColor: "#ffffff94" } }}
          variant="fullWidth"
          sx={{
            backdropFilter: "blur(16px)",
            background: "transparent",
            height: "6vh",
            minHeight: "50px",
            maxHeight: "70px"
          }}
          value={activeTabValue}
          aria-label="menu">
          <Tab
            data-test-id="tab-dashboard"
            onClick={handleTabDashboard}
            // onClick={() => !["/", "/dashboard"].includes(pathname) && navigate("/")}
            icon={
              <svg
                viewBox="0 0 24 24"
                style={{ fill: "currentColor", lineHeight: 1, transform: "scale(1.2)" }}
                className="MuiSvgIcon-root MuiSvgIcon-fontSizeSmall css-1xwgyfb-MuiSvgIcon-root"
                focusable="false"
                aria-hidden="true"
                data-testid="AccountBalanceIcon">
                <path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8z"></path>
              </svg>
            }
            aria-label="dashboard"
          />
          <Tab
            data-test-id="tab-notification"
            icon={<NotificationIcon unreadCount={pathname !== "/notifications" && unreadNotificationsCount} />}
            onClick={handleTabNotifications}
            aria-label="notifications"
          />
          <Tab onClick={handleTabMarket} data-test-id="tab-market" icon={<Logo width={"1.9em"} height={"1.9em"} />} aria-label="market" />
          <Tab onClick={handleTabBalance} data-test-id="tab-balance" icon={accountBalanceTabIcon} fontSize="small" aria-label="balance" />
          <Tab
            onClick={handleTabProfile}
            data-test-id="tab-profile"
            icon={
              <svg
                viewBox="0 0 24 24"
                style={{ fill: "currentColor", lineHeight: 1, transform: "scale(1.2)" }}
                className="MuiSvgIcon-root MuiSvgIcon-fontSizeSmall"
                focusable="false"
                aria-hidden="true">
                <path d="M12 12c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm0 2c-2.67 0-8 1.34-8 4v2h16v-2c0-2.66-5.33-4-8-4z"></path>
              </svg>
            }
            aria-label="account"
          />
        </Tabs>
      </MDBox>
    </>
  )
}
