import { Link } from "react-router-dom"
import PropTypes from "prop-types"
import Card from "@mui/material/Card"
import CardMedia from "@mui/material/CardMedia"
import MDBox from "../../../../components/MDBox"
import MDTypography from "../../../../components/MDTypography"
import locationIcon from "../../../../icons/locationIcon"
import timeIcon from "../../../../icons/timeIcon"
import { PercentageBar } from "../../../../components/PercentageBar/PercentageBar"
import trackEvent from "../../../../components/context"
function DefaultInvestmentCard({
  id,
  image,
  addSpace,
  label,
  title,
  color = "info",
  networkProgress,
  personalProgress,
  missingProgress,
  value,
  caption = "per month",
  duration,
  priority,
  loading,
  index,
  disabled,
  isDisabled
}) {
  return (
    <Link
      to={isDisabled ? isDisabled : "/investment/" + id}
      rel="noreferrer"
      sx={{ textDecoration: "none" }}
      onClick={() => {
        trackEvent("Investment", { investmentId: id })
      }}>
      <Card
        sx={{
          display: "flex",
          flexDirection: "column",
          backgroundColor: "transparent",
          boxShadow: "none",
          overflow: "visible",
          paddingBottom: addSpace ? "30px" : "10px"
        }}>
        <MDBox sx={{ aspectRatio: "16/9", backgroundColor: "#e6e7e9" }} position="relative" width="100%" shadow="xl" borderRadius="xl">
          <CardMedia
            onError={({ currentTarget }) => {
              currentTarget.onerror = null // prevents looping
              currentTarget.src = image
            }}
            width="1890"
            height="1080"
            src={image}
            component="img"
            title={title}
            sx={{
              maxWidth: "100%",
              margin: 0,
              boxShadow: ({ boxShadows: { md } }) => md,
              objectFit: "cover",
              objectPosition: "center",
              aspectRatio: "16 / 9",
              height: "auto"
            }}
            fetchpriority={priority}
            loading={loading}
          />
        </MDBox>

        <MDBox display="flex" justifyContent="space-between" pt={1.1}>
          <MDTypography variant="h5" textTransform="capitalize" fontSize={"1.15rem"}>
            {title}
          </MDTypography>
        </MDBox>
        <MDBox display="flex" justifyContent="space-between" flexDirection="column">
          <MDTypography fontSize={"0.68rem"} sx={{ transform: "translateY(1.1px)" }} fontWeight="regular" color="text">
            {locationIcon} {label}
          </MDTypography>

          <MDTypography fontSize={"0.68rem"} sx={{ transform: "translateY(1px)" }} fontWeight="regular" color="text">
            {timeIcon} {duration} months rent contract
          </MDTypography>
        </MDBox>
        <MDBox display="flex" justifyContent="space-between" mt="-1.2rem" flexDirection="column">
          <MDBox display="flex" justifyContent="end" mt="-2.75rem" height="1.6rem">
            <MDBox color={color}>
              <MDTypography component={"span"} lineHeight={1} fontSize={"1.15rem"} fontWeight="medium" color={color}>
                {value.toLocaleString("en-US")}
              </MDTypography>
              <MDTypography pl={0.3} variant={"caption"} fontWeight="medium" color={color}>
                USDT
              </MDTypography>
            </MDBox>
          </MDBox>

          <MDBox display="flex" justifyContent="flex-end">
            <MDTypography lineHeight={1.4} fontSize={"0.68rem"} fontWeight="regular" color="text">
              {caption}
            </MDTypography>
          </MDBox>
        </MDBox>
        <PercentageBar networkProgress={networkProgress} personalProgress={personalProgress} missingProgress={missingProgress} />
      </Card>
    </Link>
  )
}

// Setting default values for the props of DefaultInvestmentCard
// DefaultInvestmentCard.defaultProps = {
//   authors: [],
// }

// Typechecking props for the DefaultInvestmentCard
DefaultInvestmentCard.propTypes = {
  image: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  action: PropTypes.shape({
    type: PropTypes.oneOf(["external", "internal"]),
    route: PropTypes.string,
    color: PropTypes.string,
    label: PropTypes.string
  })
  // authors: PropTypes.arrayOf(PropTypes.object),
}

export default DefaultInvestmentCard
