import React, { useState } from "react"
import Tooltip from "@mui/material/Tooltip"
import MDBox from "../../../../components/MDBox"

const CustomTooltip = ({ title, children, disabled, width, display, justifyContent }) => {
  const [showTooltip, setShowTooltip] = useState(false)

  const handleMouseEnter = () => {
    setShowTooltip(true)
  }

  const handleMouseLeave = () => {
    setShowTooltip(false)
  }

  return (
    <MDBox style={{ width: width || "49%", cursor: "pointer", display, justifyContent }} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
      {showTooltip && (
        <Tooltip sx={{ width: "100%" }} open={true} title={disabled ? title : ""} placement="top">
          <MDBox>{children}</MDBox>
        </Tooltip>
      )}
      {!showTooltip && <MDBox>{children}</MDBox>}
    </MDBox>
  )
}

export default CustomTooltip
